.nodes {
  font-size: 11px;
	width: 16px;
	border: 2px solid white;
	word-wrap: break-word;
	padding: 2px 1px;
	text-align: center;
	line-height: 1em;
	color: #fff;
	background: #222;
  float: right;
}

.post-title {
  float: left;
}

.date-author-history-comment {
	clear: left;
}

.empty-container {
	position: relative;
	width: 0;
	height: 0;
	float: right;
}

.nodes-container {
	position: absolute;
	top: -30px;
	left: -100px;
  float: right;
  width: 96px;
	border: 0px solid black;
}

.current {
  color: blue;
}

.citation {
	border: 1px solid #ddf;
	background: #eef;
	padding: 2px 8px;
	font-size: 95%;
}

li {
	margin-top: 0.2em;
	margin-bottom: 0.2em;
}

.inc_false, 
.mathjax_true,
.chartjs_true,
.ptext_true,
.x3dom_true,
.threejs_true,
._3dmol_true,
.oo_true,
.svgphys_true {
	font-size: 11px;
}

.inc_false {
	padding: 1px 4px;
	border: 1px solid #ccc;
	color: #aaa;
}

